import React, {useState} from 'react';
import './App.css';
import scrollarrow from './assets/svg/arrowup.svg';

const ScrollArrow = () =>{

  const [showScroll, setShowScroll] = useState(false)

  const checkScrollTop = () => {
    if (!showScroll && window.pageYOffset > 800){
      setShowScroll(true)
    } else if (showScroll && window.pageYOffset <= 800){
      setShowScroll(false)
    }
  };

  const scrollTop = () =>{
    window.scrollTo({top: 0, behavior: 'smooth'});
  };

  window.addEventListener('scroll', checkScrollTop)

  return (
        <img alt="scrolltotop" src={scrollarrow} className="scrollTop" onClick={scrollTop} style={{height: 60, display: showScroll ? 'inline-block' : 'none'}}/>
  );
}

export default ScrollArrow;