import React, { Fragment } from "react";

import { Timeline, Event } from "react-timeline-scribble";

export default function MyTimeline () {
    return(
  <Fragment>
    <h3 style={{textAlign: 'center', margin : "50px 0 50px 0 ",fontSize:'32px', fontWeight: 'bolder'}}>My experience</h3>
    <hr/>
    <Timeline>
        <Event interval={"Oct 2020 - Present"} title={"ICT Upskilling 6"} subtitle={"Al Hussein Technical University - Amman, Jordan"}>
        <ul style={{marginLeft: '50px'}}>
            <li style={{listStyleType: 'square'}}>
                Cohort 6 - Web Development Technologies - Full stack development (MERN Stack).
            </li>
            <li style={{listStyleType: 'square'}}>
                Soft Skills training program.
            </li>
            <li style={{listStyleType: 'square'}}>
                English language training program.
             </li>
            
            
        </ul>
      </Event>  
      <Event interval={"Jun 2020 - Sep 2020"} title={"Summer Intern"} subtitle={"Pio-Tech Solutions - Amman, Jordan"}>
      <ul style={{marginLeft: '50px'}}>
            <li style={{listStyleType: 'square'}}>
                Overview of around 50% SAP Business Intelligence Solutions
                and Products used by Pio-tech Company.
            </li>
            <li style={{listStyleType: 'square'}}>
                Applied Business Intelligence development strategies on
                Microsoft Power BI software.
            </li>
            <li style={{listStyleType: 'square'}}>
                Worked on tasks given by trainer with groups or individually.
            </li>
            
            
        </ul>
      </Event>
      
    </Timeline>
  </Fragment>)
}

