import React from 'react';
import {Form, Button, Toast,Container} from 'react-bootstrap';
import { useState } from 'react';
function FormPage(){
const [show, setShow] = useState(false);
const showMessage  = () => { setShow(!show);};

return(
<Container className="p-3" style={{color: '#fff'}}>
    <Form action="/contact">
        <Form.Group controlId="formBasicEmail">
    <Form.Label>Email Address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" />
    <Form.Text className="">
      I will never share your email with anyone else.
    </Form.Text>
  </Form.Group>
  <Form.Group controlId="formBasicName">
    <Form.Label>Your Awesome Name</Form.Label>
    <Form.Control type="text" placeholder="Enter your name" />
    
  </Form.Group>
  <Form.Group controlId="formBasicMessage">
    <Form.Label>Your Message to me</Form.Label>
    <Form.Control as="textarea" rows="5" placeholder="Message" />
  </Form.Group>
  
  <Button onClick={() => showMessage()}  type="" className="btnContact">
    Send
  </Button>
    </Form>
    <div aria-live="polite"
  aria-atomic="true"
  style={{ color: '#000',
    position: 'relative',
    minHeight: '100px',
  }}>
    <Toast onClose={() => setShow(false)} show={show} delay={3000} autohide>
          <Toast.Header>
            <img
              src="holder.js/20x20?text=%20"
              className="rounded mr-2"
              alt=""
            />
            <strong className="mr-auto">Sent!</strong>
            <small> 2 seconds ago</small>
          </Toast.Header>
          <Toast.Body>Woohoo, I will be happy now!</Toast.Body>
        </Toast>
        </div>
        </Container>
)
}
export default FormPage;