import React from 'react';
import { Col, Jumbotron, Card } from 'react-bootstrap';
import {Container, Row} from 'react-bootstrap';
import {useSpring, animated} from 'react-spring';
import contactimg from '../assets/svg/undraw_Social_strategy_re_mk87.svg';
import '../App.css';
import FormPage from '../Form';

function ContactPage(){
     
    const style = useSpring({opacity: 1, from: {opacity: 0}});
    
    return(
        <animated.div style={style}  className="bg-contact">

        <Jumbotron  className="bg-transparent m-auto">
        
        <Container fluid={true}>
                
                <Row className="align-items-center w-100">
                    <Col md={6} className="mx-auto">
                    
            <div>
                <h1 id="letstalk" style={{padding:'4rem 0 0 4rem'}}>
                    Let's Talk
                </h1>
            </div>
        
                <img  src={contactimg} alt="img" width="90%"/>
                    </Col>
                    <Col md={6} className="mx-auto">
                <Card className="shadow p-0 mx-auto rounded" style={{background: ' rgba(0, 0, 0, 0.3)'}}>
                <FormPage></FormPage>
                </Card>
                </Col>
                </Row>
        </Container>
        </Jumbotron>
        </animated.div>
    );
}

export default ContactPage;