import React from 'react';
import {Container,Row,Col} from 'react-bootstrap';
import './App.css';

function Footer (){

    return(
        <footer  >
            <Container fluid={true} className="bg-dark footer">
                <Row className="justify-content-around p-4">
                    <Col className="p-0 " md={4} sm={3} />
                    <Col className="p-0 d-flex justify-content-end" sm={6}>
                        <p>
                            Made with <span role="img" aria-label="orange heart"> üß° </span> by Lana Al Ramahi
                        </p>
                    </Col>
                </Row>
            </Container>
        </footer>
    )

}

export default Footer;