import React from 'react';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Switch
  } from "react-router-dom";
import {Container, Navbar, NavbarBrand, Nav} from 'react-bootstrap';
import Footer from 'Footer';
import HomePage from 'pages/HomePage';
import ProjectsPage from 'pages/ProjectsPage';
import ContactPage from 'pages/ContactPage';
import logo from './assets/svg/undraw_female_avatar_w3jk.svg';
import ScrollArrow from './ScrollArrow';

class Home extends React.Component {
  render(){
    return (
        <Router >
            <Container className="mh-100 p-0" fluid={true}>
                <ScrollArrow />
                <Navbar className="text-white" expand="md"  variant="light" bg="dark" sticky="">
                    <NavbarBrand><img src={logo} className="App-logo" alt="logo" /> <strong className="pl-2 logo-title ">Lana AlRamahi</strong> </NavbarBrand>
                    <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"></Navbar.Toggle>
                    <Navbar.Collapse id="navbar-toggle" >
                        <Nav className="ml-auto">
                            <Link className="nav-link text-white customLink" to="/my-portfolio">Home</Link>
                            <Link className="nav-link text-white customLink" to="/projects">Projects</Link>
                            <Link className="nav-link text-white customLink" to="/contact">Contact</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>   
                <Switch>
                    <Route path="/my-portfolio" exact component={HomePage}></Route>
                    <Route path="/projects"  component={ProjectsPage}></Route>
                    <Route path="/contact"  component={ContactPage}></Route>
                </Switch>
            <Footer />
            </Container>
            
        </Router>
        
        );
  }
  
  
    
}

export default Home;
